.form__label {
    display: block;
    margin-left: auto;
    margin-right: auto;

}
.form__input {
  margin-bottom: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 15px 28px 16px 28px;
  background-color: transparent;
  border: 2px solid #ff0081;
  box-sizing: border-box;
  color: grey;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.08em;
  cursor: pointer;
}
     
     
     
     
     
   
     
     
     
     
     
   